* {
  margin: 0;
  border: 0 solid;
  box-sizing: border-box;
}

body {
  font-family: Segoe WPC, Segoe UI, sans-serif;
  background: rgb(51, 51, 51);
  display: flex;
  flex-direction: column;
  height: 100vh;
}

#app {
  height: inherit;
}

.editor {
  height: inherit;
  padding: 6px;
  gap: 6px;
  display: flex;
}

.preview {
  background: rgb(37 37 38);
  color: #d4d4d4;
  width: 100%;
  padding: 6px;
  overflow-x: hidden;
}

.preview h1,
.preview h2,
.preview h3,
.preview h4,
.preview h5,
.preview h6 {
  margin-top: 24px;
  margin-bottom: 16px;
  font-weight: 600;
  line-height: 1.25;
  border-bottom: 1px solid #333;
  padding-bottom: 0.3em;
}

.preview a {
  color: #0969da;
  text-decoration: none;
}

.preview a:hover {
  text-decoration: underline;
}

.preview pre {
  padding: 4px;
  overflow: auto;
  line-height: 1.45;
  background-color: #1e1e1e;
}

.preview table {
  display: block;
  width: 100%;
  width: max-content;
  max-width: 100%;
  overflow: auto;
}

.preview table tr {
  background-color: #1e1e1e;
  border-top: 1px solid #333;
}

.preview p {
  margin-top: 0;
  margin-bottom: 16px;
}

.open-file {
  position: absolute;
  background-color: rgb(51, 51, 51);
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.open-file:after {
  content: "ðŸ“„ Open fileâ€¦";
  color: #d4d4d4;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.hide {
  display: none;
}

textarea {
  background: #1e1e1e;
  color: #d4d4d4;
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 14px;
  font-feature-settings: "liga" 0, "calt" 0;
  line-height: 19px;
  letter-spacing: 0px;
  width: 100%;
  resize: none;
  padding: 6px;
}

textarea:focus {
  outline: none;
}
